<template>
	<div>
		<label
			v-if="label"
			:class="labelClass"
			class="input-label"
			:for="id"
		>
			<slot>{{label}}</slot>
		</label>
		<slot name="element"></slot>
	</div>
</template>

<script>
  export default {
    props: {
      label: String,
	    labelClass: {
        type: String,
		    default: ''
	    },
	    id: String
    },
	  methods: {

	  }

  }
</script>

<style lang="scss">
	.input-label {
		display: inline-flex;
		cursor: pointer;
		font-size: 16px;
		font-weight: 400;
		color: #fff;
	}
	%for-text {
		display: block;
		width: 100%;
		padding: 15px;
		height: 48px;
		font-family: 'Roboto', sans-serif;
		font-size: 16px;
		line-height: 1.2em;
		border-color: transparent;
		border-radius: 4px;
		color: #fff;
		background: #353539;
		letter-spacing: 0;
		outline: none;
		transition: all 0.5s ease;
	}

	%for-focus {
		border-color: #ffc107;
	}
	.v-text {
		@extend %for-text;
	}
	.v-text::placeholder {
		color: #868e96;
		opacity: 1;
	}

	.v-text:disabled, .v-text[readonly] {
		background-color: #e9ecef;
		opacity: 1;
	}

	.v-text::-ms-expand {
		background-color: transparent;
		border: 0;
	}

	.v-text:focus {
		@extend %for-focus;
		&::placeholder {
			visibility: hidden;
		}
	}
	@media screen and (min-width: 812px) {
		.input-label {
			color: #000;
		}
		.v-text {
			background: transparent;
			color: #324354;
			border: 1px solid #DCDCDC;
		}
	}
</style>