<template>
	<div class="modal-bg" ref="modalBg" @click="close">
			<div :class="[typeModalClass, modalClass]" >
				<span class="modal-close" ref="modalClose">&times;</span>
				<div class="modal-container">
					<slot></slot>
				</div>
			</div>
	</div>
</template>

<script>
  export default {
    name: 'Modal',
    props: {
      video: Boolean,
      modalClass: String,
    },
    mounted() {
      window.addEventListener('keyup', this.keyClose);
    },
    computed: {
      typeModalClass() {
        return this.video ? 'modal-video' : 'modal-wrap'
      }
    },
    methods: {
      keyClose(event) {
        if (event.keyCode === 27) {
          this.$emit('close');
        }
      },
      close(event) {
        if (event.target === this.$refs.modalBg || event.target === this.$refs.modalClose) {
          this.$emit('close');
        } else return false;
      },
    },
    destroyed() {
      window.removeEventListener('keyup', this.keyClose)
    }
  }
</script>

<style lang="scss">
	.modal {
		&-bg {
			position: fixed;
			align-items: center;
			justify-content: center;
			z-index: 300;
			min-height: 100vh;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background: rgba(#000, .5);
			padding: 20px 10px;
			overflow-y: auto;
		}
		&-wrap {
			position: relative;
			z-index: 310;
			max-width: 512px;
			display: flex;
			flex-direction: column;
			margin: 0 auto;
			padding: 60px 16px;
			box-shadow: 0 3px 10px -0.5px rgba(0, 0, 0, 0.2);
			pointer-events: auto;
			background: #1d1d1d;
			overflow: hidden;
		}
		&-close {
			position: absolute;
			top: 0;
			right: 0;
			padding: 5px;
			line-height: .5;
			font-size: 40px;
			color: #fff;
			cursor: pointer;
		}
		@media screen and (min-width: 812px) {
			&-wrap {
				background: #fff;
				border-radius: 10px;
				min-width: 600px;
				padding: 60px 40px;
			}
			&-close {
				color: #324354;
				padding: 14px;
				font-size: 38px;
			}
		}
	}
</style>