<template>
	<v-control-wrap
		type="radio"
		:label="label"
		:labelClass="labelClass"
		:id="id"
	>
		<template #input>
			<input
				class="v-radio v-input"
				type="radio"
				:id="id"
				:value="value"
				:checked="shouldBeChecked"
				@change="updateInput"
			/>
		</template>
	</v-control-wrap>
</template>

<script>
	import VControlWrap from './VControlWrap'
  export default {
	  components: {
      VControlWrap
	  },
    model: {
      prop: 'modelValue',
      event: 'change'
    },
    props: {
      id: {
        type: String,
        required: true
      },
      label: {
        type: String,
	      required: true
      },
      labelClass: {
        type: String,
        default: ''
      },
	    value: String,
	    modelValue: {
		    default: ''
      }
    },
	  computed: {
	    shouldBeChecked() {
	      return this.modelValue === this.value
	    },
	    model: {
	      get() {
	        return this.modelValue === this.value;
	      },
		     set(val) {
	        this.$emit('change', val);
		     }
	    }
	  },
	  methods: {
	    updateInput() {
	      this.$emit('change', this.value)
	    }
	  }
  }
</script>

<style lang="scss">
	.v-control-radio {
		.label-radio {
			z-index: 10;
			&:before {
				z-index: 0;
				top: -3px;
				width: 20px;
				height: 20px;
				background: #f8f8f8;
				border: 1px solid #ccc;
				border-radius: 50%;
				box-shadow: inset 0 2px 3px 2px rgba(18,18,17, 0.5);
			}
			&:after {
				display: block;
				width: 12px;
				height: 12px;
				border-radius: 50%;
				background: #ffc107;
				left: 4px;
				top: 1px;
				z-index: 1;
				/*transform: scale(.6);*/
				opacity: 0;
				box-shadow: inset 0 1px 1px 2px rgba(#000, .2), 0 0 5px 1px rgba(#ffc107,1);
			}

		}
		& input:checked + .control-label {
			&:before {
				box-shadow: inset 0 0 1px 1px rgba(#333, .3);
				border: none;
				z-index: 1;
			}
			&:after {
				opacity: 1;
				transform: translateY(0);
			}
			transform: scale(1);
		}
	}
</style>