<template>
	<button
		class="v-btn"
		:type="typeBtn"
		@click.prevent="$emit('click')"
	>
		<span>
			<slot></slot>
		</span>
	</button>

</template>

<script>
  export default {
    name: 'VButton',
    props: {
      typeBtn: {
        type: String,
        default: 'button'
      }
    }
  }
</script>

<style lang="scss">
	.v-btn {
		display: flex;
		align-items: center;
		justify-content: center;
		text-transform: uppercase;
		color: #111;
		white-space: nowrap;
		border: none;
		background: none;
		border-radius: 4px;
		transition: all 0.5s ease;
		cursor: pointer;
		box-shadow: 0 3px 1px -2px rgba(0, 0, 0, .2),
								0 2px 2px 0 rgba(0, 0, 0, .14),
								0 1px 5px 0 rgba(0, 0, 0, .12);

		&[disabled] {
			box-shadow: none;
			opacity: 0.7;
			border: 1px solid #dcdcdc;
			color: rgba(#324354, .5);
			background: #dcdcdc;
			pointer-events: none;
		}
	}

</style>