<template>
	<v-btn
		:class="[`v-btn-${btnStyle}`, 'v-button']"
		:typeBtn="type"
		@click="$emit('click')"
	>
		<slot>Сохранить</slot>
	</v-btn>
</template>

<script>
  import VBtn from '../../atoms/VBtn';

  export default {
    props: {
      type: String,
      btnStyle: {
        type: String,
        default: 'reg'
      },
      modified: {
        type: Boolean,
        default: false
      }
    },
    components: {
      VBtn
    },
    computed: {
      customClass() {
        return (this.modified) ? `v-btn-${this.modified}` : ''
      }
    }
  }
</script>

<style lang="scss">
	.v-button {
		padding: 0 15px;
		height: 40px;
		font-size: 16px;
		font-weight: 700;
	}
	.v-btn {
		&-reg {
			background: #ffc107;;
			&:hover {
				background: #ffa800;
			}
		}
		&-empty {
			background: transparent;
			border: 1px solid #ffc107;
			color: #fff;
			&:hover {
				background: #ffc107;
			}
		}
		&-lg {
			background: #ffc107;
			padding: 0 54px;
			height: 60px;
			width: 100%;
			max-width: 416px;
		}
	}
	@media screen and (min-width: 812px) {
		.v-btn-empty {
			color: #000;
		}
	}

</style>