<template>
	<modal-wrap modalClass="modal-target-settings" @close="close">
		<form action="#!" @submit.prevent="formSub">
			<div class="row">
				<v-combo-select
					label="Выберите Страну"
					placeholder="Выберите"
					:options="countries"
					@selection="selectCountry"
					search-filter
				/>
			</div>
			<div class="row">
				<v-combo-select
					label="Город"
					placeholder="Выберите"
					:options="cities"
					@selection="selectCity"
					search-filter
				/>
			</div>
			<div class="row">
				<v-combo-select label="Пол" :options="sexList" select-one/>
			</div>
			<div class="row">
				<p class="select-label">Возраст</p>
				<div class="flex-row">
					<v-text-field
						class="input-wrap"
						type="text"
						placeholder="от"
						v-model="ageFrom"
					/>
					<v-text-field
						class="input-wrap"
						type="text"
						placeholder="до"
						v-model="ageTo"
					/>
				</div>
			</div>
			<div class="row">
				<v-checkbox label="Только пользователи онлайн"
				            id="online-users"
				            v-model="usersOnline"
				/>
			</div>
			<div class="row">
				<v-text-field
					class="input-wrap"
					type="text"
					label="ID группы VK"
					id="for-vk-group"
					placeholder="например: 123456789"
					v-model="groupId"
				/>
				<v-instructions @click="isOpen = !isOpen" text="Где брать ID группы VK?"/>
				<v-button
					class="mt-20"
					type="submit"
					@click.prevent="add"
				>Добавить
				</v-button>
			</div>
			<div class="row">
				<table-modal class="target-settings-table"></table-modal>
			</div>
		</form>
		<modal-instruction id="vkGroup" v-if="isOpen" @close="isOpen = false"/>
	</modal-wrap>
</template>

<script>
  import ModalWrap from './ModalWrap'
  import ModalInstruction from './ModalInstruction'
  import VComboSelect from '../../molecules/inputs/VComboSelect'
  import VTextField from '../../molecules/inputs/VTextField'
  import VCheckbox from '../../molecules/inputs/VCheckbox'
  import VInstructions from '../info/VInstructions'
  import VButton from '../../molecules/buttons/VButton'
  import TableModal from '../table/TableForModal'
  import {mapActions, mapGetters, mapState} from 'vuex'

  export default {
    components: {
      ModalWrap,
      ModalInstruction,
      VComboSelect,
      VTextField,
      VCheckbox,
      VButton,
      VInstructions,
      TableModal
    },
    data() {
      return {
        sexList: [
          'Любой', 'Мужской', 'Женский'
        ],
        isOpen: false,
        country: '',
        city: '',
        sex: '',
        ageFrom: '',
        ageTo: '',
        groupId: '',
        usersOnline: false,
        ageValid: {
          from: null,
          to: null,
          text: ''
        },

      }
    },
    computed: {
      ...mapState(['cities']),
      ...mapGetters(['countries']),
      ageRules() {
        let from = +this.ageFrom;
        let to = +this.ageTo;

        if (from > to) {
          this.ageValid.text = 'Начальный возраст не может быть больше предельного'
          this.ageValid.to = false;
          return this.ageValid;
        }
        if (!from) {
					this.ageValid.text = ''
        }
      }
    },
    methods: {
      ...mapActions(['selectCountry', 'selectCity']),
      close() {
        this.$emit('close');
      },
      add() {

      },
      formSub() {

      }
    }
  }
</script>

<style lang="scss">
	.modal-target-settings {
		height: auto;
		.row {
			margin-bottom: 20px;
		}
		.select-label {
			margin-bottom: 4px;
		}
		.input-label {
			margin-bottom: 6px;
		}
	}

	/*.target-settings-table {*/
	/*max-height: 100%;*/
	/*}*/
	.flex-row {
		.input-wrap {
			margin-bottom: 20px;
			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	@media screen and (min-width: 812px) {
		.flex-row {
			display: flex;
			justify-content: space-between;
			.input-wrap {
				width: 47%;
				margin-bottom: 0;
			}
		}
	}

	@media screen and (min-width: 1224px) {
		.modal-target-settings {
			min-width: 1000px;
			max-width: 57vw;
		}
	}

</style>