<template>
	<div class="v-nav-panel">
		<the-sidebar :class="{hide: isHide}"/>
		<the-header  :class="{hide: isHide}"/>
	</div>
</template>

<script>
  import TheSidebar from './TheSidebar'
  import TheHeader from './TheHeader'

  export default {
    components: {
      TheSidebar,
      TheHeader
    },
    data: () => {
      return {
        isHide: false,
      }
    },
    mounted() {
      window.addEventListener('scroll', this.toHide);
    },
    methods: {
      toHide() {
        pageYOffset > 200 ? this.isHide = true : this.isHide = false
      }
    },
    beforeDestroy() {
      window.removeEventListener('scroll', this.toHide);
    }
  }
</script>

<style lang="scss">
	.v-nav-panel {
		position: fixed;
		z-index: 50;
		top: 0;
		left: 0;
		width: 100%;
		display: flex;
		flex-direction: column;
		@media screen and (min-width: 1024px) {
			flex-direction: row;
		}
	}
	.hide {
		opacity: 0;
		@media screen and (min-width: 812px) {
			opacity: 1;
		}
	}


</style>