<template>
	<div class="v-tab">
		<div class="v-tab-container">
			<button
				v-for="(obj,name,index) in data"
				class="v-tab-title"
				@click.prevent="selectTab(index)"
				:class="{'is-active': (index === currentTab)}"
				:key="name"
			>{{ obj.title}}
			</button>
		</div>
		<transition name="slide-appearance" mode="out-in">
			<div
				v-for="(obj, name, index) in data"
				class="v-tab-content"
				v-if="currentTab === index"
				:key="index"
			>
				<v-text-area
					v-if="(currentTab === index)"
					class="v-tab-textarea"
					:key="index"
					:placeholder="obj.placeholder"
					v-model="obj.model"
				/>
				<!--<slot name="content">male keep-alive</slot>-->
			</div>
		</transition>
	</div>
</template>

<script>
  import VTextArea from '../inputs/VTextArea'

  export default {
    components: {
      VTextArea
    },
    props: {
      data: [Object, Array],
      keepAlive: {
        type: Boolean,
        default: false,
      },
      value: {
        type: String,
        default: '',
      },
    },

    data() {
      return {
        currentTab: 0,
      }
    },
    computed: {
      model: {
        get() {
          return this.value;
        },
        set(val) {
          this.$emit('input', val);
        }
      },
    },
    methods: {
      selectTab(selectTab) {
        if (selectTab === this.currentTab) return false;
        else this.currentTab = selectTab;
      },
      priorityModel(def, obj) {
        return def ? def : obj
      }
    }
  }
</script>

<style lang="scss">
	.v-tab {
		&-container {
			display: inline-flex;
			margin-bottom: 10px;
		}
		&-title {
			border: 1px solid #dcdcdc;
			font-family: 'Roboto', sans-serif;
			border-radius: 4px 4px 0 0;
			background: transparent;
			padding: 5px 10px;
			font-size: 18px;
			font-weight: 500;
			color: #fff;
			margin-right: 10px;
			&:last-child {
				margin-right: 0;
			}
			&:focus {
				box-shadow: 0 0 10px rgba(#dcdcdc, .8)
			}
			&.is-active {
				color: #000;
				background: #ffc107;
				border-color: #ffc107;
				box-shadow: 0 3px 1px -2px rgba(0, 0, 0, .2),
				0 2px 2px 0 rgba(0, 0, 0, .14),
				0 1px 5px 0 rgba(0, 0, 0, .12);
			}
			@media screen and (min-width: 812px) {
				color: rgba(#324354, .5);
			}
		}
		&-content {
			position: relative;
		}
		/*&-textarea {*/
		/*position: absolute;*/
		/*width: 100%;*/
		/*top: 0;*/
		/*left: 0;*/
		/*}*/
	}
</style>