<template>
	<div
		class="v-control"
		:class="`v-control-${type}`"
	>
		<slot name="input"></slot>
		<label
			v-if="label"
			class="control-label"
			:class="[`label-${type}`, labelClass]"
			:for="id"
		>
			<slot>{{label}}</slot>
		</label>
	</div>
</template>

<script>
  export default {
    props: {
      type: String,
      id: {
        type: String,
      },
      label: {
        type: String,
	      default: ' ',
      },
      labelClass: {
        type: String,
        default: ''
      }
    }
  }
</script>

<style lang="scss">
	.v-control [type="checkbox"],
	.v-control [type="radio"],
	.v-control .v-input {
		display: none;
	}

	/*
	======================================================
		Labels
	======================================================
	*/
	.control-label {
		position: relative;
		display: inline-flex;
		align-items: flex-start;
		cursor: pointer;
		font-size: 12px;
		color: rgba(#fff, .7);
		padding-left: 26px;
		transition: color 0.5s ease;
		&:after,
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			flex-shrink: 0;
			transition: all 0.5s ease;
		}
		&:before {
			left: 0;
			&:hover {
				box-shadow: 0 0 40px rgba(0,0,0,1);
			}
		}
		@media screen and (min-width: 812px) {
			color: #324354;
		}
	}
</style>