<template>
	<v-input-wrap
		:label="label"
		:labelClass="labelClass"
		:id="id"
	>
		<template #element>
			<textarea
				class="v-textarea v-text"
				:class="(customClass ? customClass : '')"
				:id="id"
				:placeholder="placeholder"
				:value="value"
				@input="updateTextArea"
			>
			</textarea>
		</template>
	</v-input-wrap>
</template>

<script>
  import VInputWrap from './VInputWrap'

  export default {
    components: {
      VInputWrap
    },
	  props: {
      label: String,
		  labelClass: {
        type: String,
			  default: ''
		  },
      customClass: {
        type: String,
	      default: ''
      },
		  id: String,
		  placeholder: String,
		  value: String
	  },
	  methods: {
      updateTextArea(event) {
        this.$emit('input', event.target.value);
      }
	  }
  }
</script>

<style lang="scss">
	.v-textarea {
		overflow: auto;
		resize: vertical;
		min-height: 96px;
		max-height: 140px;
	}
</style>