<template>
	<v-input-wrap
		:label="label"
		:labelClass="labelClass"
		:id="id"
	>
		<template #element>
			<input
				v-filter="filter"
				type="text"
				class="v-text"
				:class="(customClass ? customClass : '')"
				:id="id"
				:placeholder="placeholder"
				:value="value"
				@input="updateValue"
			/>
		</template>
	</v-input-wrap>
</template>

<script>
  import VInputWrap from './VInputWrap'

  export default {
    directives: {
      filter: {
        bind(el, bindings) {
          el.oninput = function(e) {
            console.log(e.which);
          }
        }
      }
    },
	  name: 'VTextField',
    components: {
      VInputWrap
    },
    props: {
	    filter: {
	      type: String,
	    },
      id: String,
      label: String,
      labelClass: String,
      customClass: {
        type: String,
	      default: ''
      },
      placeholder: String,
      type: {
        type: String,
        default: 'text'
      },
	    value: String
    },
    data() {
      return {
	      handler: ''
      }
    },
    computed: {
    },
	  methods: {
      updateValue(event) {
        return this.$emit('input', event.target.value)
      }
	  }

  }
</script>

