<template>
	<div class="combo-select"
	     :class="{'no-valid': !isValid}"
	     tabindex="0"
	     @blur="isBlur"
	>
		<div class="select-label"
		>{{label}}</div>
		<div class="custom-select">
			<span class="toggle-arrow"
			      :class="{'toggle-arrow--rotate': isOpen}"
			>
				<svg width="1em" height="1em">
					<use xlink:href="#icon-arrow-select">
						<svg id="icon-arrow-select" viewBox="0 0 20 20">
                <path d="M3.225 5.40832L1.75 6.88332L10 15.1333L18.25 6.88333L16.775 5.40833L10 12.1833L3.225 5.40832Z"
                      fill="currentColor"></path>
            </svg>
					</use>
				</svg>
			</span>
			<div class="selected"
			     @click="isOpen = !isOpen"
			>
				{{selectedValue}}
			</div>
			<transition name="slide-top">
				<div class="dropdown options"
				     v-show="isOpen">
					<div class="options__search-item" v-if="searchFilter">
						<input class="input-search v-text"
						       type="text"
						       v-model="searchVal"
						       placeholder="Поиск"
						       ref="searchFilter"
						>
					</div>
					<div class="options__item"
					     v-for="(option, index) of options"
					     :key="index"
					     :class="{'is-chosen': selected === option}"
					     @click="selection(option, index, $event)"
					>
						{{option}}
					</div>
				</div>
			</transition>
		</div>
		<select-error v-if="!isValid && !selected"/>
	</div>
</template>

<script>
  import VTextField from './VTextField'
  import SelectError from '../../components/info/SelectError'

  export default {
    components: {
      VTextField,
      SelectError
    },
	  props: {
      options: Array,
		  tabindex: String,
		  label: String,
		  placeholder: String,
		  searchFilter: Boolean,
		  selectOne: Boolean,
		  isValid: {
        type: Boolean,
			  default: false,
		  },
	  },
    data() {
      return {
        isOpen: false,
        searchVal: '',
	      selected: '',
        isChosen: false,
      }
    },
	  mounted() {
      this.selected = this.selectOne ? this.options[0] : ''
	  },
	  computed: {
      selectedValue() {
	      if (!this.selected) {
	        return this.placeholder
        }
	      else {
	        return this.selected;
        }
      }
	  },
	  methods: {
      isBlur(e) {
        if (e.relatedTarget === this.$refs.searchFilter) {
          return false;
        } else {
          this.isOpen = false;
        }
      },
      // selection(option) {
      //   this.selected = option;
      // },
      selection(option, id,event) {
        this.selected = option;
        this.isChosen = true;
        id += 1;
        if (event.target !== this.$refs.searchFilter) {
          this.isOpen = !this.isOpen;
          this.$emit('selection', {option, id});
        } else return false;
      },
		  searchElem() {

		  }
	  }
  }
</script>

<style lang="scss">
	.combo-select {
		outline: none;
		:focus {
			.custom-select {
				border: 1px solid #ffc107;
			}
		}
	}
	.custom-select {
		position: relative;
		height: 51px;
		white-space: nowrap;
		text-overflow: ellipsis;
		border: 1px solid transparent;
		border-radius: 4px;
		font-size: 16px;
		color: #fff;
		background: #353539;
		cursor: pointer;
		transition: all 0.3s ease;
		&:hover {
			border-color: #ffc107;
		}
		&:focus {
			border-color: #ffc107;
			outline: none;
			background: #fff;
		}
	}
	.selected {
		border-radius: 4px;
		background: #353539;
		padding: 15px 35px 15px 15px;
	}
	.options {
		position: absolute;
		top: 50px;
		left: 0;
		z-index: 1000;
		width: 100%;
		padding-bottom: 10px;
		background: #353539;
		transition: all 0.5s ease;
		box-shadow: 0 3px 7px rgba(46, 80, 165, 0.29);
		max-height: 260px;
		overflow-y: auto;
		border-radius: 4px;
		&__item {
			padding: 10px 25px;
			&:hover {
				background: rgba(255, 193, 7, 0.2);
			}
		}
	}
	.options__item {
		&.is-chosen {
			background: #ffc107;
			color: #000;
		}
	}
	.options__search-item {
		position: relative;
		padding: 10px 15px;
		z-index: 200;
	}
	.select-label {
		color: #fff;
	}

	.input-search {
		border: 1px solid rgba(#fff,.5);
	}


	@media screen and (min-width: 812px) {
		.custom-select {
			background: transparent;
		}
		.options {
			background: #fff;
			color: #000;
			padding-bottom: 10px;
		}
		.selected {
			background: transparent;
			color: #000;
		}
		.custom-select {
			border-color: #dcdcdc;
		}
		.toggle-select svg {
			color: #324354 ;
		}
		.select-label {
			color: #000;
		}
		.input-search {
			border-color: #dcdcdc;
		}
	}
</style>