<!--TODO: уточнить логику выделения элементов таблицы для удаления -->

<template>
	<div>
		<transition name="slide-appearance" mode="out-in">
			<div class="table-mobile">
				<div class="table-mobile__headers">
					<div class="table-mobile__header"
					     v-for="(header, key) of tableMobile.headers"
					     :key="key"
					>
						{{header}}
					</div>
				</div>
				<transition-group class="table-mobile__accordion"
				                  name="slide-appearance"
				                  tag="div"
				>
					<div class="table-mobile__row"
					     v-for="(obj, idx) of tableMobile.body"
					     :key="obj.id"
					>
						<div class="table-mobile__item"
						     @click="openDrop(obj.id)"
						     :class="{'is-active': isShow && openId === obj.id}"
						>
								<span v-for="(item,key) in obj.row"
								      :key="key"
								>
									{{item}}
								</span>
							<span class="toggle-arrow"
							      :class="{'toggle-arrow--rotate': isShow && openId === obj.id}"
							>
								<svg width="1em" height="1em">
								<use xlink:href="#icon-arrow-select">
									<svg id="icon-arrow-select" viewBox="0 0 20 20">
										<path
											d="M3.225 5.40832L1.75 6.88332L10 15.1333L18.25 6.88333L16.775 5.40833L10 12.1833L3.225 5.40832Z"
											fill="currentColor"></path>
									</svg>
								</use>
							</svg>
							</span>
						</div>
						<transition name="down">
							<ul :key="idx" class="table-mobile__dropdown" v-show="isShow && openId === obj.id">
								<li class="table-mobile__dropdown-item"
								    v-for="(item,key) of obj.dropdown"
								    :key="key"
								>
										<span v-for="(n, index) of item"
										      :key="index"
										>{{n}}
										</span>
								</li>
								<li>
									<v-button btn-style="empty" @click="deleteItem(obj)">Удалить</v-button>
								</li>
							</ul>
						</transition>
					</div>
				</transition-group>
			</div>
		</transition>
		<transition name="slide-appearance" mode="out-in">
			<div class="v-table">
				<div class="v-table__header">
					<div class="v-table__col"
					     v-for="header of table.headers"
					>{{header}}
					</div>
					<div class="v-table__col">
						<v-button class="btn-check-all"
						          @click="checkAll"
						          :disabled="isDisabled"
						>{{btnText}}
						</v-button>
					</div>
				</div>
				<transition-group name="slide-appearance"
				                  class="v-table__body"
				                  tag="div"
				>
					<div v-for="(obj, index) of this.table.data"
					     class="v-table__row"
					     :key="obj.id"
					>
						<div v-if="prop !== obj.id"
						     class="v-table__col"
						     v-for="(prop,index) of obj"
						     :key="index"
						     :title="prop"
						>
							<span class="v-table__text">
								{{ prop }}
							</span>
						</div>
						<div class="v-table__col">
							<v-checkbox
								:value="obj.id"
								:id="'id_' + obj.login"
								v-model="deleteElems"
							/>
							<!--<v-button-->
							<!--class="btn-remove"-->
							<!--btn-style="empty"-->
							<!--@click="accountDelete(obj.id)"-->
							<!--&gt;Удалить-->
							<!--</v-button>-->
						</div>
					</div>
					<div class="table-row" v-if="!table.data.length" :key="'no-data'">
						<span class="no-data">Нет добавленных акканутов</span>
					</div>
				</transition-group>
				<transition name="slide-appearance">
					<button-wave v-if="deleteElems.length"
					             @click="removeTable"
					>Удалить
					</button-wave>
				</transition>
			</div>
		</transition>
	</div>
</template>

<script>
  import VButton from '../../molecules/buttons/VButton'
  import ButtonWave from '../../molecules/buttons/ButtonWave'
  import VCheckbox from '../../molecules/inputs/VCheckbox'

  export default {
    components: {
      VButton,
      ButtonWave,
      VCheckbox
    },
    props: {
      tableData: Object
    },
    data() {
      return {
        currentPage: 1,
        perPage: 5,
        table: [],
        tableMobile: [],
        openId: null,
        deleteElems: [],
        selectedAll: false,
        clicked: false,
        isShow: false,
      }
    },
    created() {
      this.table = this.tableData.table;
      this.tableMobile = this.tableData.tableMobile;
    },
    computed: {
      isDisabled() {
        return !this.deleteElems.length && !this.table.data.length
      },
      btnText() {
        let el_length = this.deleteElems.length;
        let table_length = this.table.data.length;

        if (!table_length && !el_length) {
          return 'Нет аккаунтов'
        }
        if (el_length > table_length / 2 || el_length === table_length) {
          this.selectedAll = true;
          return 'Снять выделение'
        }
        if (!el_length || (el_length <= table_length / 2)) {
          this.selectedAll = false;
          return 'Выделить все'
        }

      },
      selected() {
        let result = [];

      },
      visibleTable() {
        return this.table.data.splice((this.currentPage - 1) * this.perPage, (this.currentPage - 1) * this.perPage + this.perPage)
      },
    },
    methods: {
      isChecked(id) {
        return (this.deleteElems.includes(id)) ? 1 : 0
      },
      openDrop(id) {
        if (this.openId === id && this.isShow) {
          this.isShow = false
        } else {
          this.openId = id;
          this.isShow = true;
        }
      },
      checkAll() {
        this.selectedAll = !this.selectedAll;
        if (this.selectedAll) {
          this.table.data.forEach(el => {
            this.deleteElems.push(el.id);
          });
        } else {
          this.deleteElems.splice(0, this.deleteElems.length);
        }
      },
      accountDelete(id) {
        if (!this.isChecked(id)) return;
        this.table.data.forEach((item, i) => {
          if (item.id === id) {
            // item.splice(i, 1);
            this.table.data.splice(i, 1);
          }
        })
      },
      removeTable() {
        this.deleteElems.forEach(id => {
          this.table.data.forEach((obj, i) => {
            if (obj.id === id) {
              this.table.data.splice(i, 1);
            }
          });
        });
        this.deleteElems.splice(0, this.deleteElems.length);
      },
      deleteItem(obj) {
        this.tableMobile.body.splice(this.tableMobile.body.indexOf(obj), 1);
      }
    }
  }
</script>
<style lang="scss">
	.v-table {
		display: none;
	}

	.table-mobile {
		width: 100%;
		color: #fff;
		&__headers {
			display: flex;
			justify-content: space-between;
			padding-bottom: 10px;
			border-bottom: 1px solid rgba(222, 222, 222, 0.8);
			div {
				flex-basis: calc(100% / 3);
				text-align: center;
			}
		}
		&__row {
			overflow: hidden;
			border-bottom: 1px solid rgba(222, 222, 222, 0.2);
		}
		&__item {
			position: relative;
			display: flex;
			justify-content: space-between;
			background: rgba(50, 67, 84, 0.2);
			&.is-active {
				background: rgba(50, 67, 84, .9);
			}
			span {
				flex-basis: calc(100% / 3);
				text-align: center;
				padding: 10px 5px;
			}
		}
		&__dropdown {
			max-height: 300px;
			height: 100%;
			margin-bottom: 20px;
			&-item {
				display: flex;
				justify-content: space-between;
				border-bottom: 1px solid #dedede;
				padding: 12px;
			}

		}
		.v-btn {
			margin-top: 20px;
			margin-left: auto;
		}
		.tab-toggle {
			position: absolute;
			top: 0;
			right: 0;
			color: #fff;
		}
	}

	.table-row-enter-active, .table-row-leave-active {
		transition: all .3s;
	}

	.table-row-enter, .table-row-leave-to {
		opacity: 0;
		transform: translateY(-10px);
	}

	.fade-enter-active, .fade-leave-active {
		transition: all .3s;
	}

	.fade-enter, .fade-leave-to {
		opacity: 0;
		transform: translateX(10px);
	}

	@media screen and (min-width: 812px) {
		.table-mobile {
			display: none;
		}
		.v-table {
			position: relative;
			display: flex;
			flex-direction: column;
			min-width: 57vw;
			.control-label {
				&:before,
				&:after {
					top: -15px;
				}
			}
			&__header {
				display: flex;
				color: #1d1d1d;
				font-size: 16px;
				font-weight: 700;
				margin-bottom: 20px;
			}
			&__header,
			&__body {
				width: 100%;
			}
			&__row {
				position: relative;
				display: flex;
				align-items: center;
				width: 100%;
				border-bottom: 1px solid rgba(0, 0, 0, .2);
				padding: 10px 0;
				&:hover .v-btn {
					opacity: .7;
					&:hover {
						opacity: 1;
					}
				}
			}
			&__col {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				cursor: pointer;
				flex-basis: calc(100% / 6);
			}
			&__text {
				text-align: center;
				width: 100%;
				overflow: hidden;
				text-overflow: ellipsis;
				padding: 5px 0;
			}
			&__btn-remove {
				font-size: 12px;
				font-family: 'Roboto', sans-serif;
				font-weight: 600;
				color: #000;
				text-transform: uppercase;
				border: none;
				border-radius: 6px;
				padding: 10px;
			}
			.waves-btn {
				position: absolute;
				right: 0;
				bottom: -10px;
				transform: translateY(100%);
				min-width: 108px;
				width: 100%;
				max-width: 12%;
				&__text {
					color: #000;
				}
				.waves {
					&:hover {
						transform: translate(30px, 60px)
					}
					&:before {
						background: rgba(#f8f8f8, .8);
					}
					&:after {
						background: rgba(#f8f8f8, .4);
						border-radius: 40%;
					}
				}
			}
			.btn-check-all {
				width: 100%;
				font-size: 14px;
				text-transform: none;
				white-space: normal;
			}
		}
	}

	@media screen and (min-width: 1024px) and (max-width: 1200px) {
		.v-table__col .btn-remove {
			display: none;
		}
	}

	.down-enter-active, .down-leave-active {
		transition: max-height .5s linear, opacity .4s ease;
	}

	.down-enter, .down-leave-to {
		max-height: 0;
		opacity: 0;
	}
</style>