<template>
		<button
			class="waves-btn v-btn v-button"
			@click="$emit('click')"
		>
			<span class="waves-btn__text">
				<slot></slot>
			</span>
			<span class="waves"></span>
		</button>
</template>

<script>
  export default {
  }
</script>

<style lang="scss">
	.waves-btn {
		position: relative;
		overflow: hidden;
		background: #ffc107;
		transition: background .2s ease-in .6s;
		//box-shadow: 0 3px 10px -2px rgba(#ffc107, 0.2),
		//0 2px 8px 5px rgba(#ffc107, 0.14),
		//0 1px 5px 0 rgba(#ffc107, 0.12);
		box-shadow: 0 5px 7px 0 rgba(255, 193, 7, 0.24);
		&:hover {
			.waves {
				transition: transform 1s ease-in .2s;
				transform: translate(-62px, -30px);
			}
		}
		&__text {
			pointer-events: none;
			position: relative;
			z-index: 10;
			color: #fff;
		}
		@media screen and (min-width: 812px) {
			box-shadow: 0 3px 3px -2px rgba(#000, 0.2),
			0 2px 2px 0 rgba(#000, 0.14),
			0 1px 5px 0 rgba(#000, 0.12);
		}
	}

	.waves {
		position: absolute;
		background: #ffc107;
		bottom: 0;
		left: calc((200px - 100%) / -2);
		transition: transform .5s ease-out .3s;
		/*overflow: hidden;*/
		z-index: 1;
		width: 200px;
		height: 200px;
		&:after,
		&:before {
			content: '';
			position: absolute;
			z-index: 9;
			top: -10%;
			left: 0;
			width: 100%;
			height: 100%;
			transition: .3s;
		}
		&:after {
			border-radius: 48%;
			background: rgba(#1d1d1d, 1);
			animation: rotate 25s infinite ease;

		}
		&:before {
			border-radius: 42%;
			background: rgba(#1d1d1d, .6);
			animation: rotate 20s infinite ease-in;
		}
	}

	@keyframes rotate {
		100% {
			transform: rotate(360deg);
		}
	}


</style>