<template>
	<v-control-wrap
		type="checkbox"
		:label="label"
		:labelClass="labelClass"
		:id="id"
	>
		<template #input>
			<input
				class="v-checkbox v-input"
				type="checkbox"
				:id="id"
				:value="value"
				:checked="shouldBeChecked"
				@change="updateInput"
			/>
		</template>
	</v-control-wrap>
</template>

<script>
	import VControlWrap from './VControlWrap'
  export default {
	  model: {
	    prop: 'modelValue',
		  event: 'change'
	  },
	  components: {
      VControlWrap
	  },
    props: {
      label: String,
      labelClass: {
        type: String,
	      default: ''
      },
	    modelValue: {
        default: false
	    },
	    value: [String, Number],
      id: {
        type: String,
      },
	    trueValue: {
        default: true
	    },
	    falseValue: {
        default: false
	    }
    },
	  computed: {
	    shouldBeChecked() {
		    if(this.modelValue instanceof Array) {
		      return this.modelValue.includes(this.value)
		    }
		    return this.modelValue === this.trueValue
	    },
	    model: {
	      get() {
	        return this.value
	      },
		    set(val) {
	        this.$emit('input', val);
		    }
	    }
	  },
	  methods: {
	    updateInput(event) {
	      let isChecked = event.target.checked;

	      if(this.modelValue instanceof Array) {
	        let newValue = [...this.modelValue]

		      if(isChecked) newValue.push(this.value);
		      else newValue.splice(newValue.indexOf(this.value),1);

		      this.$emit('change', newValue)
	      } else {
	        this.$emit('change', isChecked ? this.trueValue : this.falseValue)
	      }
	    }
	  }
  }
</script>

<style lang="scss">

	.v-control-checkbox {
		.label-checkbox {
			&:before {
				border: 2px solid #324354;
				border-radius: 2px;
				width: 18px;
				height: 18px;
			}
			&:after {
				border-radius: 4px;
				opacity: 0;
				z-index: 10;
				width: 18px;
				height: 18px;
				background:  url("../../../static/icons8-checked-checkbox-24.png") no-repeat center,#fff;
			}
		}
		& input:checked + .control-label {
			&:after {
				opacity: 1;
			}
		}
	}

</style>