<template>
	<modal-wrap video @close="$emit('close')" :role="modalLink">
		<div class="modal-custom__inner-video">
			<iframe
				width="100%"
				height="100%"
				:src="`https://www.youtube.com/embed/${this.modalLink}`"
				frameborder="0"
				allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
				allowfullscreen="">
			</iframe>
		</div>
	</modal-wrap>
</template>

<script>
	import ModalWrap from '../modal/ModalWrap'
  export default {
    name: 'Modal',
	  components: {
      ModalWrap
	  },
	  props: {
      id: {
        type: String,
	      require: false
      }
	  },
    data() {
      return {
        modalLinks: null,
      }
    },
	  created() {
      this.modalLinks = new Map([
        ['vk', 'MUFvuxkbAvQ'],
        ['greeting', 'Lb19C23ApJQ'],
	      ['dialogs', 'zE0HKLnXjxI'],
	      ['target', 'aZVNVCkGe60'],
	      ['reposting', 'AISR3ETAAQ4'],
	      ['accounts', 'mgULMnkF3P4'],
	      ['vkGroup', 'flGc3uzOnKw']
      ]);
	  },
	  computed: {
      modalLink() {
        if (!this.id) {
          return this.modalLinks.get(this.$store.getters.modalId);
        } else return this.modalLinks.get(this.id);
		  }
	  }
  }
</script>

<style lang="scss">
	.modal-video {
		z-index: 300;
		position: absolute;
		max-width: 915px;
		width: 100%;
		top: 50%;
		left: 50%;
		transform: translate(-50%,-50%);
		background: #000;
		border-radius: 10px;
		box-shadow: 0 3px 10px -0.5px rgba(0, 0, 0, 0.2);
		.modal-close {
			top: -50px;
			right: 0;
			color: #fff;
			&:hover {
				color: #ffc107;
			}
		}
		@media screen and (min-width: 982px) {
			.modal-close {
				top: -50px;
				right: -50px;
				&:hover {
					color: #ffc107;
				}
			}
		}
	}

	.modal-custom__inner-video {
		position: relative;
		border-radius: 10px;
		overflow: hidden;
		&:before {
			padding-bottom: 56.25%;
			content: " ";
			display: block;
			visibility: hidden;
		}
		iframe {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}
	}


</style>