<template>
	<div class="sex-switcher">
		<div class="sex-switcher-row" :class="{'switch-sex': isMale}">
			<div class="sex-switcher__item">
				<button class="sex-switcher__btn" :class="{'is-active': !isMale}" @click="isMale = false">Женские</button>
			</div>
			<div class="sex-switcher__item">
				<button class="sex-switcher__btn" :class="{'is-active': isMale}" @click="isMale = true">Мужские</button>
			</div>
		</div>
		<v-text-area placeholder="login:password"/>
	</div>
</template>

<script>
  import VBtn from '../../atoms/VBtn'
  import VTextArea from '../../molecules/inputs/VTextArea'

  export default {
    components: {
      VBtn,
      VTextArea
    },
    data() {
      return {
        isMale: false,
      }
    },
    methods: {
      switchSex() {
        this.isMale = !this.isMale
      }
    }
  }
</script>

<style lang="scss">
	.sex-switcher {
		cursor: pointer;
		margin-bottom: 20px;
		max-width: 400px;
		min-width: 200px;
		&-row {
			position: relative;
			display: flex;
			border-radius: 5em;
			overflow: hidden;
			user-select: none;
			margin-bottom: 20px;
			box-shadow: inset 0 0 8px 3px rgba(#000, .6);
			background: rgba(#1E1E23,.8) radial-gradient(circle at 100% 100%, rgba(#1E1E23,.60), rgba(#000,.20));
			padding: 2px 0;
			&:before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				border-radius: 5em;
				transform: translateX(-50%);
				background: #ffc107;
				animation: female-switch .5s forwards;
				//box-shadow: 0 3px 1px -2px rgba(0,0,0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0);
				box-shadow: 0 3px 15px 3px rgba(0, 0, 0, 0.7), 0 2px 2px 0 rgba(0, 0, 0, 0.84), 0 0 0 12px rgba(255, 255, 0, .5);


			}
			&.switch-sex {
				&:before {
					animation: male-switch .5s forwards;
				}
				@keyframes male-switch {
					0% {
						transform: translateX(-50%);
					}
					50% {
						transform: translateX(0)
					}
					100% {
						transform: translateX(50%);
					}
				}
				@keyframes female-switch {
					0% {
						transform: translateX(50%);
					}
					50% {
						transform: translateX(0)
					}
					100% {
						transform: translateX(-50%);
					}
				}
			}
		}
		@keyframes rotate-female {
			50% {
				transform: rotateY(-45deg);
			}
		}
		@keyframes rotate-male {
			50% {
				transform: rotateY(45deg)
			}
		}
		&__item {
			width: 50%;
		}
		&__btn {
			position: relative;
			z-index: 1;
			display: flex;
			width: 100%;
			border-radius: 5em;
			justify-content: center;
			border: 0;
			background: transparent;
			font-size: 16px;
			font-weight: 600;
			padding: 10px;
			color: #fff;
			transition: color .2s ease .4s;
			&.is-active {
				color: #000;
			}
		}
		@media screen and (min-width: 812px) {
			&-row {
				box-shadow: inset 0 0 8px 3px rgba(#dcdcdc, .6);
				background: rgba(#dcdcdc,.8) radial-gradient(circle at 100% 100%, rgba(#fefefe,.60), rgba(#dcdcdc,.20));
				&:before {
					box-shadow: 0 0 10px 2px rgba(#ccc, 1),
					inset -6px 0 6px rgba(#1d1d1d, .14),
					inset 6px 0 6px rgba(#1d1d1d, .14);
				}
			}
			&__btn {
				color: rgba(#000,.5);
			}
		}
	}

</style>