<template>
	<div class="v-block" :class="customClass">
		<h3 v-if="blockTitle"
		    class="v-block-title"
		>
			{{ blockTitle }}
		</h3>
		<slot></slot>
	</div>
</template>

<script>
  export default {
    props: {
      blockName: String,
      blockTitle: String
    },
	  computed: {
      customClass() {
       return this.blockName ? `block-${this.blockName}` : ''
      }
	  }
  }
</script>

<style lang="scss">
	.v-block {
		&-title {
			font-family: 'Roboto', sans-serif;
			font-weight: 500;
			font-size: 26px;
			margin-bottom: 18px;
		}
	}
</style>